<template>
    <div class="sidebar" v-if="sidebar">
        <div class="sidebar-close" @click="close">&times;</div>
        <div class="sidebar-content">
            <img src="../assets/logo.png" alt="VUE CLI">
            <div class="title">
                <h3>What is <a href="https://vuejs.org/">VUE</a>?</h3>
            </div>
            <div class="sidebar-content">
                <h5>Vue is a JavaScript framework for building user interfaces.
                    It builds on top of standard HTML, CSS and JavaScript, and
                    provides a declarative and component-based programming model
                    that helps you efficiently develop user interfaces, be it simple or complex.
                </h5>
            </div>
        </div>
    </div>
</template>

<script>
    import {useStore} from 'vuex'
    import {computed} from 'vue'
    export default {
        setup(){
            const store = useStore()
            const sidebar = computed( ()=>{
                return store.state.sidebar
            })


            return{
                sidebar,
                close:()=>{
                    store.commit('closeSidebar')
                }
            }
        }
    }
</script>

<style scoped>
    *{
        color: black;
        text-align: center;
    }
    h5{
        animation-name: rang;
        animation-duration: 4s;
        animation-timing-function: linear;
        animation-iteration-count: infinite;
    }
    a{
        text-decoration: none;
    }
    @keyframes rang {
        0%{
            color: #222222;
        }
        25%{
            color: red;
        }
        50%{
            color: blue;
        }
        75%{
            color: green;
        }
        100%{
            color: yellow;
        }
    }
</style>